.Container {
  border-radius: 50% 50% 50% 0;
  background: #89849b;
  transform: rotate(-45deg);
  border: 1px solid #f5f5f53d;
  margin-bottom: 50%;
  filter: contrast(1.2);
}

.Container:after {
  content: '';
  width: 50%;
  height: 50%;
  margin: 25% 0 0 25%;
  background: #FFFFFF;
  position: absolute;
  border-radius: 50%;
  box-shadow: inset 0px 1px 3px #e6c7c7;
}
