.Container_GameConfiguration__3u6jB button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  font-size: 16px;
  font-family: 'Bungee', cursive;
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm {
  color: #000;
  text-transform: uppercase;
  padding: 10px 20px;
  width: 32%;
  background: #fff;
  border: 2px solid #ddd;
  border-radius: 0.75em;
  transform-style: preserve-3d;
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), background 150ms cubic-bezier(0, 0, 0.58, 1);
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #ddd;
  border-radius: inherit;
  box-shadow: 0 0 0 2px #ddd, 0 0em 0 0 #ffe3e2;
  transform: translate3d(0, 0.75em, -1em);
  transition: transform 150ms cubic-bezier(0, 0, 0.58, 1), box-shadow 150ms cubic-bezier(0, 0, 0.58, 1);
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm:hover {
  background: #fff;
  transform: translate(0, 0.25em);
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm:hover::before {
  /* box-shadow: 0 0 0 2px #b18597, 0 0.5em 0 0 #ffe3e2; */
  transform: translate3d(0, 0.5em, -1em);
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm:active {
  background: #fff;
  transform: translate(0em, 0.75em);
}
.Container_GameConfiguration__3u6jB button.Container_playbtn__2agqm:active::before {
  /* box-shadow: 0 0 0 2px #b18597, 0 0 #ffe3e2; */
  transform: translate3d(0, 0, -1em);
}

